import { defineComponent } from "vue";
import Container from "../components/common/Container.vue";
import Tree from "../assets/axe/tree.jpg";
import TreeCracked from "../assets/axe/tree_cracked.png";
import Down from "../assets/axe/down.jpg";
import Fireplace from "../assets/axe/fireplace.jpg";
import Axe1 from "../assets/axe/axe_1.png";
import Axe2 from "../assets/axe/axe_2.png";
var Step;
(function (Step) {
    Step[Step["First"] = 10] = "First";
    Step[Step["Second"] = 20] = "Second";
    Step[Step["Third"] = 30] = "Third";
    Step[Step["Final"] = 50] = "Final";
})(Step || (Step = {}));
;
export default defineComponent({
    components: { Container },
    data() {
        const data = {
            Step,
            logs: 0,
            isCutting: false,
        };
        return data;
    },
    methods: {
        time() {
            if (this.logs < Step.First)
                return 300;
            else if (this.logs < Step.Second)
                return 500;
            else if (this.logs < Step.Third)
                return 800;
            else
                return 1200;
        },
        onCut() {
            this.isCutting = true;
            setTimeout(() => {
                this.isCutting = false;
                ++this.logs;
            }, this.time());
        }
    },
    computed: {
        tree() {
            if (this.logs < Step.First)
                return Tree;
            if (this.logs < Step.Second)
                return TreeCracked;
            else if (this.logs < Step.Final)
                return Down;
            else
                return Fireplace;
        },
        axe() {
            return this.logs < Step.First ? Axe1 : Axe2;
        },
    },
});
