import { defineComponent } from "vue";
import Container from "../components/common/Container.vue";
import { RefreshLeft } from '@element-plus/icons-vue';
import ResetButton from '../components/common/ResetButton.vue';
export default defineComponent({
    components: { Container, RefreshLeft, ResetButton },
    data() {
        return {
            text: "🌙’🏝🥷🐉🏝🐶🐘 🇶🇦🇺🇦🐘 🚂🇺🇦 🐶🦉🐘🦈🐶🦉🐘👩‍🔬 🐘👩‍🔬🚂 🌙🕷 🌙🐘🚂🚂🦈🐘 🧙🏻‍♂️, ⛵️🦈🕷🍷🫒!",
            textUpdated: "🌙’🏝🥷🐉🏝🐶🐘 🇶🇦🇺🇦🐘 🚂🇺🇦 🐶🦉🐘🦈🐶🦉🐘👩‍🔬 🐘👩‍🔬🚂 🌙🕷 🌙🐘🚂🚂🦈🐘 🧙🏻‍♂️, ⛵️🦈🕷🍷🫒!",
            hieroglyphs: [
                { h: "🌙", r: "" },
                { h: "🏝", r: "" },
                { h: "🥷", r: "" },
                { h: "🐉", r: "" },
                { h: "🐶", r: "" },
                { h: "🐘", r: "" },
                { h: "🇶🇦", r: "" },
                { h: "🇺🇦", r: "" },
                { h: "🚂", r: "" },
                { h: "🦉", r: "" },
                { h: "🦈", r: "" },
                { h: "👩‍🔬", r: "" },
                { h: "🕷", r: "" },
                { h: "🧙🏻‍♂️", r: "" },
                { h: "⛵️", r: "" },
                { h: "🍷", r: "" },
                { h: "🫒", r: "" },
            ],
        };
    },
    watch: {
        hieroglyphs: {
            handler() {
                var textUpdated = this.text;
                this.hieroglyphs.forEach((hieroglyph) => {
                    if (hieroglyph.r.length > 0)
                        textUpdated = textUpdated.replaceAll(hieroglyph.h, hieroglyph.r);
                });
                this.textUpdated = textUpdated.replace(/^\w/, (c) => c.toUpperCase());
                ;
            },
            deep: true,
        },
    },
    methods: {
        onReset() {
            this.hieroglyphs = this.hieroglyphs.map((hieroglyph) => ({ h: hieroglyph.h, r: "" }));
        }
    }
});
