import { defineComponent } from "vue";
import { Aim } from "@element-plus/icons-vue";
import ResetButton from "../common/ResetButton.vue";
export default defineComponent({
    components: { ResetButton, Aim },
    props: {
        startingMatrix: { type: Object, required: true },
        answerMatrix: { type: Object, required: true },
        answer: { type: String, required: true },
    },
    data() {
        const data = {
            matrix: this.startingMatrix.map((r) => r.map((c) => c)),
            ball: undefined,
            verify: undefined,
        };
        return data;
    },
    computed: {
        message() {
            if (this.ball) {
                return `Déposez ${this.ball.ball}`;
            }
            else {
                return "Choisissez une balle";
            }
        }
    },
    methods: {
        onSelectCell(ball, i, j) {
            this.verify = undefined;
            // Drop ball
            if (this.ball) {
                this.matrix[this.ball.i][this.ball.j] = undefined;
                this.matrix[i][j] = this.ball.ball;
                this.ball = undefined;
            }
            // Pick up ball
            else if (ball) {
                this.ball = { ball, i, j };
            }
        },
        disableCell(cell) {
            if (this.ball) {
                return cell && cell !== this.ball.ball;
            }
            else if (cell) {
                return false;
            }
            return true;
        },
        onReset() {
            this.verify = undefined;
            this.matrix = this.startingMatrix.map((r) => r.map((c) => c));
        },
        onVerify() {
            for (let i = 0; i < this.matrix.length; ++i) {
                for (let j = 0; j < this.matrix[i].length; ++j) {
                    if (this.matrix[i][j] !== this.answerMatrix[i][j]) {
                        this.verify = false;
                        return;
                    }
                }
            }
            this.verify = true;
        },
    }
});
